const Label = styled.label({
  color: theme.colors.grey_400,
})

const copyToClipboard = str => {
  const el = document.createElement('textarea')
  el.value = str
  document.body.appendChild(el)
  el.select()
  document.execCommand('copy')
  document.body.removeChild(el)
}

const indent = (value, indent = 2) =>
  value.replace(/^(.+)$/gm, match => ' '.repeat(indent) + match).trim()

const formatCode = (value, type) => {
  const code = `(function() {
    var data = __JSON__;
    var script = document.createElement('script');
    script.type = 'application/ld+json';
    script.innerHTML = JSON.stringify(data);
    document.head.appendChild(script);
  })();`

  switch (type) {
    default:
    case 'prettified':
      const formattedCode = prettier.format(code, {
        parser: 'babel',
        plugins: [parserBabylon],
      })
      return `<script>\n${formattedCode}</script>\n<!--Google Tag Manager Friendly JSON-LD Code Generated by https://saijogeorge.com/json-ld-schema-generator/tag-manager-fix/-->`.replace(
        '__JSON__',
        indent(JSON.stringify(JSON.parse(value), null, 2))
      )
    case 'minified':
      const { code: minifiedCode } = UglifyJS.minify(code)
      return `<script>${minifiedCode}</script><!--Google Tag Manager Friendly JSON-LD Code Generated by https://saijogeorge.com/json-ld-schema-generator/tag-manager-fix/-->`.replace(
        '__JSON__',
        JSON.stringify(JSON.parse(value))
      )
  }
}

const addScriptTags = value =>
  `<script type="application/ld+json">\n${value}\n</script>`

const stripScriptTags = value =>
  value.replace(/<\/?script[^>]*(>|$)(<!--.*)?/gim, '')

// Change this:
// "Paragraph
//  paragraph
//  paragraph"
// to: "Paragraph\nparagraph\nparagraph"
const escapeLineBreaks = value =>
  value.replace(/("[\s|\S|\n]*?")/g, match => {
    return match.replace(/[\n|\r]/g, '\\n')
  })

const ChangeDirectionButton = ({ changeDirection, ...props }) => (
  <button
    onClick={changeDirection}
    {...props}
    css={{
      display: 'flex',
      justifyContent: 'center',
      alignItems: 'center',
      padding: 20,
    }}
  >
    <svg
      viewBox="0 0 24 24"
      css={{
        display: 'block',
        width: 24,
        height: 24,
        fill: '#999',
        transform: 'rotate(90deg)',
        transition: '.2s',
        [media.tabletLg]: {
          transform: 'none',
        },
        '*:hover > &': {
          fill: theme.colors.blue,
        },
      }}
    >
      <path d="M6.3 14.4h8v2h-8v3.08L2 15.4l4.3-4.08v3.08zM22 8.6l-4.3-4.08V7.6h-8v2h8v3.08L22 8.6z" />
    </svg>
  </button>
)

const useTagManagerFixState = createEditorState('tag-manager-fix__draft')

const TagManagerFixForm = ({ changeDirection, codeFromOtherGenerator }) => {
  const [input, setInput] = useTagManagerFixState(codeFromOtherGenerator)
  const [output, setOutput] = useState('')
  const [outputType, setOutputType] = useState('minified')
  const [error, setError] = useState(false)
  const formattedOutput = output && !error ? formatCode(output, outputType) : ''

  useEffect(() => {
    setError(false)

    if (input === '') {
      setOutput('')
      return
    }

    if (!input) {
      setError(true)
      setOutput('')
      return
    }

    const jsonInput = escapeLineBreaks(stripScriptTags(input))

    try {
      JSON.parse(jsonInput)
    } catch {
      setError(true)
      setOutput('')
      return
    }

    setOutput(jsonInput)
  }, [input])

  return (
    <div
      css={{
        display: 'grid',
        gridTemplateColumns: '1fr',
        padding: rem(20),
        [media.tabletLg]: {
          gridTemplateColumns: '1fr auto 1fr',
          padding: rem(30),
        },
        [min(1200)]: {
          padding: rem(40),
        },
        background: '#fff',
        borderRadius: 8,
        boxShadow: '0 5px 30px #CBD6D9',
      }}
    >
      <div>
        <Label htmlFor="input-code">JSON-LD input</Label>
        <Space y={10} />
        <TextArea
          id="input-code"
          autosize={{ minRows: 8 }}
          value={input}
          placeholder="Paste code here"
          onChange={e => {
            setInput(e.target.value)
          }}
          css={{
            maxHeight: 'calc(100vh - 150px) !important',
            fontFamily: 'monospace',
            resize: 'none',
            '&.ant-input': { height: 178 },
          }}
        />
        <Space y={20} />
        {error && (
          <Fragment>
            <Alert
              message={
                <form
                  method="post"
                  target="_blank"
                  action="https://search.google.com/structured-data/testing-tool"
                >
                  <p css={{ display: 'inline' }}>Please enter valid code.</p>
                  <Space inline x={10} />
                  <Button type="ghost" size="small" htmlType="submit">
                    Test on Google
                  </Button>
                  <textarea
                    name="code"
                    value={input}
                    readOnly
                    css={{ display: 'none' }}
                  />
                </form>
              }
              type="error"
            />
            <Space y={20} />
          </Fragment>
        )}
      </div>
      <ChangeDirectionButton onClick={changeDirection} />
      <div id="output" css={{ marginTop: rem(-14) }}>
        <Label
          htmlFor="output-code"
          css={{
            display: 'inline-block',
            paddingTop: rem(14),
          }}
        >
          Google Tag Manager Friendly Code
        </Label>
        <Space y={10} />
        <TextArea
          id="output-code"
          autosize={{ minRows: 8 }}
          value={formattedOutput}
          disabled={!output}
          readOnly
          css={{
            maxHeight: 'calc(100vh - 150px) !important',
            fontFamily: 'monospace',
            resize: 'none',
            '&.ant-input': { height: 178 },
          }}
        />
        <Space y={20} />
        <div
          css={{
            display: 'flex',
            flexWrap: 'wrap',
            justifyContent: 'space-between',
            alignItems: 'center',
            marginTop: -20,
            marginLeft: -20,
            '> *': {
              marginTop: 20,
              marginLeft: 20,
            },
          }}
        >
          <Button
            type="primary"
            size="default"
            disabled={!output}
            onClick={() => {
              copyToClipboard(formattedOutput)
              message.success('Copied to clipboard!')
            }}
          >
            Copy Tag Manager Code
          </Button>
          <div css={{ color: theme.colors.grey_200 }}>
            <Radio.Group
              value={outputType}
              onChange={e => setOutputType(e.target.value)}
              size="large"
              disabled={!output}
            >
              <Radio value="minified">Minified</Radio>
              <Radio value="prettified">Prettified</Radio>
            </Radio.Group>
            <a
              href="#should-i-choose-prettified-or-minified-version"
              title="Should I choose Prettified OR Minified version?"
            >
              (What is this?)
            </a>
          </div>
        </div>
      </div>
    </div>
  )
}

const extractJSON = value => {
  const prettifiedRegEx = /var\s\w+\s*=([\w|\W]+)(;\s*var\s)/
  const minifiedRegEx = /var\s\w+\s*=([\w|\W]+),.+document\.createElement/

  if (prettifiedRegEx.test(value)) {
    const [, object] = prettifiedRegEx.exec(value) || []
    return JSON.parse(object)
  }

  const [, object] = minifiedRegEx.exec(value) || []
  return JSON.parse(object)
}

const useTagManagerFixToJsonState = createEditorState(
  'tag-manager-fix-to-json__draft'
)

const TagManagerFixToJsonForm = ({ changeDirection }) => {
  const [input, setInput] = useTagManagerFixToJsonState()
  const [output, setOutput] = useState('')
  const [error, setError] = useState(false)

  useEffect(() => {
    if (input === '') {
      setOutput('')
      setError(false)
      return
    }

    if (!input) {
      setError(true)
      setOutput('')
      return
    }

    let extractedJSON
    try {
      extractedJSON = extractJSON(input)
      extractedJSON = JSON.stringify(extractedJSON, null, 2)
      setError(false)
    } catch {
      setError(true)
      setOutput('')
      return
    }

    setOutput(addScriptTags(extractedJSON))
  }, [input])

  return (
    <div
      css={{
        display: 'grid',
        gridTemplateColumns: '1fr',
        // gridGap: rem(20),
        padding: rem(20),
        [media.tabletLg]: {
          gridTemplateColumns: '1fr auto 1fr',
          // gridGap: rem(30),
          padding: rem(30),
        },
        [min(1200)]: {
          // gridGap: rem(40),
          padding: rem(40),
        },
        background: '#fff',
        borderRadius: 8,
        boxShadow: '0 5px 30px #CBD6D9',
      }}
    >
      <div>
        <Label htmlFor="input-code">Google Tag Manager Friendly Code</Label>
        <Space y={10} />
        <TextArea
          id="input-code"
          autosize={{ minRows: 8 }}
          value={input}
          placeholder="Paste code here"
          onChange={e => {
            setInput(e.target.value)
          }}
          css={{
            maxHeight: 'calc(100vh - 150px) !important',
            fontFamily: 'monospace',
            resize: 'none',
            '&.ant-input': { height: 178 },
          }}
        />
        <Space y={20} />
        {error && (
          <Fragment>
            <Alert
              message={
                <p css={{ display: 'inline' }}>
                  Please enter valid Tag Manager Fix code.
                </p>
              }
              type="error"
            />
            <Space y={20} />
          </Fragment>
        )}
      </div>
      <ChangeDirectionButton onClick={changeDirection} />
      <div id="output" css={{ marginTop: rem(-14) }}>
        <Label
          htmlFor="output-code"
          css={{
            display: 'inline-block',
            paddingTop: rem(14),
          }}
        >
          JSON-LD code
        </Label>
        <Space y={10} />
        <TextArea
          id="output-code"
          autosize={{ minRows: 8 }}
          value={output}
          disabled={!output}
          readOnly
          css={{
            maxHeight: 'calc(100vh - 150px) !important',
            fontFamily: 'monospace',
            resize: 'none',
            '&.ant-input': { height: 178 },
          }}
        />
        <Space y={20} />
        <div
          css={{
            display: 'flex',
            flexWrap: 'wrap',
            justifyContent: 'space-between',
            alignItems: 'center',
            marginTop: -20,
            marginLeft: -20,
            '> *': {
              marginTop: 20,
              marginLeft: 20,
            },
          }}
        >
          <Button
            type="primary"
            size="default"
            disabled={!output}
            onClick={() => {
              copyToClipboard(output)
              message.success('Copied to clipboard!')
            }}
          >
            Copy JSON Code
          </Button>
        </div>
      </div>
    </div>
  )
}

const Form = ({ location, codeFromOtherGenerator }) => {
  const [form, setForm] = useState('TagManagerFixForm')

  const changeDirection = () =>
    setForm(form =>
      form === 'TagManagerFixForm'
        ? 'TagManagerFixToJsonForm'
        : 'TagManagerFixForm'
    )

  return form === 'TagManagerFixForm' ? (
    <TagManagerFixForm
      codeFromOtherGenerator={codeFromOtherGenerator}
      changeDirection={changeDirection}
    />
  ) : (
    <TagManagerFixToJsonForm changeDirection={changeDirection} />
  )
}

